import firebase from "@firebase/app";
export default {
  namespaced: true,
  state: {
    db: null,
    posts: [],
    currentPost: {},
  },
  mutations: {
    setDB: function(state) {
      if (state.db == null) {
        state.db = firebase.firestore();
      }
    },
    setPosts: function(state, data) {
      state.posts = data;
    },
    setCurrPost: function(state, data) {
      state.currentPost = data;
    },
    setPostImages: function(state, data) {
      if (data.index != -1) {
        state.posts[data.index].images = data.images;
      } else {
        state.currentPost.images = data.images;
      }
    },
  },
  actions: {
    async getPosts({ commit, dispatch }) {
      const snapshot = await firebase
        .firestore()
        .collection("posts")
        .get();
      const tempDoc = snapshot.docs.map((doc) => {
        return {
          id: doc.id,
          ...doc.data(),
        };
      });
      commit("setPosts", tempDoc);
      dispatch("setImages");
    },
    async getPost({ commit, dispatch, state }, id) {
      commit("setDB");
      var docRef = state.db.collection("posts").doc(id);
      docRef
        .get()
        .then(function(doc) {
          if (doc.exists) {
            commit("setCurrPost", {
              id: doc.id,
              ...doc.data(),
            });
            dispatch("setPostImages",doc.id);
          } else {
            console.log("No such document!");
          }
        })
        .catch(function(error) {
          console.log("Error getting document:", error);
        });
    },
    setImages({ commit, dispatch, state }) {
      state.posts.forEach((post, index) => {
        dispatch("getImages", { docID: post.id, index: index });
      });
    },
    async getImages({ commit }, data) {
      const snapshot = await firebase
        .firestore()
        .collection("images")
        .where("postId", "==", data.docID)
        .limit(1)
        .get();

      const tempDoc = snapshot.docs.map((doc) => {
        return { id: doc.id, ...doc.data() };
      });
      //this.posts[data.index].images = tempDoc;
      commit("setPostImages", { index: data.index, images: tempDoc });
    },
    setPostImages({ commit, dispatch, state }, id) {
      dispatch("getPostImages", id);
    },
    async getPostImages({ commit }, id) {
      const snapshot = await firebase
        .firestore()
        .collection("images")
        .where("postId", "==", id)
        .get();

      const tempDoc = snapshot.docs.map((doc) => {
        return { id: doc.id, ...doc.data() };
      });
      //this.posts[data.index].images = tempDoc;
      commit("setPostImages", { index: -1, images: tempDoc });
    },
  },
};
