import { createStore } from "vuex";
import user from "./modules/user";
import posts from "./modules/posts";
import firebase from "firebase/app";

export default createStore({
  state: {
    showLoader: true,
    db: null,
    imagesURLs: [],
    //posts: [],
  },
  mutations: {
    toggleLoader: function(state) {
      state.showLoader = true;
      setTimeout(() => {
        state.showLoader = false;
      }, 200);
    },
    startLoader: function(state) {
      state.showLoader = true;
    },
    endLoader: function(state) {
      state.showLoader = false;
    },
    initializeFirestore: function(state) {
      if (state.db == null) {
        state.db = firebase.firestore();
      }
    },
    addImageURL: function(state, fileURL) {
      state.imagesURLs.push(fileURL);
    },
    restartImages: function(state) {
      state.imagesURLs = [];
    },
    /*setPosts: function(state, data) {
      state.posts = data;
    },*/
  },
  actions: {
    async createPost({ commit, dispatch, state }, data) {
      commit("initializeFirestore");
      await state.db
        .collection("posts")
        .add({
          title: data.title,
          description: data.description,
          category: data.category,
          price: data.price,
          user: data.user,
        })
        .then(function(docRef) {
          dispatch("loopImages", docRef.id);
        })
        .catch(function(error) {
          console.error("Error writing document: ", error);
        });
    },
    loopImages({ commit, dispatch, state }, docID) {
      state.imagesURLs.forEach((url) => {
        let image = { postId: docID, url: url };
        dispatch("saveImageURL", image);
      });
      commit("restartImages");
    },
    async saveImageURL({ commit, state }, image) {
      await state.db
        .collection("images")
        .doc()
        .set(image);
    },
    async uploadImage({ commit }, imageFile) {
      const storageRef = firebase.storage().ref();
      const fileRef = storageRef.child(imageFile.name);
      await fileRef.put(imageFile);
      const fileURL = await fileRef.getDownloadURL();
      commit("addImageURL", fileURL);
    },
  },
  getters: {
    imagesURLs: ({ imagesURLs }) => imagesURLs,
    posts: ({ posts }) => posts,
  },
  modules: {
    user,
    posts,
  },
});
